    left: 0px;
    width: 50px;
    height: 50px;
    border-left: 2px solid #6bb8be;
    border-top: 2px solid #6bb8be;
}
.self-text p:last-child:after {
    content: "";
    display: inline-block;
    position: absolute;
    bottom: 0px;
    right: 0px;
    width: 50px;
    height: 50px;
    border-right: 2px solid #6bb8be;
    border-bottom: 2px solid #6bb8be;
}

